.profile-picture {
    border-radius: 50%;
    width: 50%;
    height: auto;
    max-width: 300px;
    margin: 30px;
}

a {
    color: #4CAF50;
    text-decoration: none;
}

.container {
    margin: 10px
}

.post {
    margin-top: 10px;
    width: 100px;
}

.title {
    margin-top: 20px;
    padding: 10px;
    text-align: center;
}

.social-icon {
    margin: 10px;
}

.post-card {
    margin-top: 10px;
}

.published {
    margin-top: 20px;
}

.ContactContainer {
    padding: 16px;
    width: 100%;
}

.Card {
    width: 100%;
    position: relative;
    box-sizing: border-box;
}

.CardContent {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 12px;
}

.SocialCardContainer:not(:first-child) {
    margin-top: 16px;
}

.CardImage {
    height: 40px;
    width: 40px;
    padding: 8px;
}

.CardText {
    margin-left: 18px;
    font-size: 18px;
    color: #222;
}

.social-icon {
    margin: 10px;
}

body {
    margin: 0;
    padding: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

